.competence-item {
    margin-top: 70px;
    text-align: center;
    font-family: Playfair Display;
    font-weight: 400;
    font-size: 22px;
    color: #413d4b;
}

.competence-text {
    text-align: center;
    margin-top: 15px;
    font-family: Playfair Display;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.3;
    color: #716e78;
}

.competence-flex {
    display: flex;
    justify-content: center;
}

.play-work:nth-child(3n) {
        break-after: always; /* Новый синтаксис */
      
}

.play-work {
    margin-bottom: 100px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.play-expirinse {
    width: 100%;
    
}

@media screen and (min-width: 768px) {
    .play-expirinse {
        width: 33.333%;
    }
}

@media screen and (min-width: 1200px) {
    .play-expirinse {
        width: 33.333%;
    }
}

.box-shadow-expierens {
    box-shadow: 1px 1px 0px rgb(0 0 0 / 0%);
}

@media screen and (min-width: 768px) {
    .box-shadow-expierens {
        box-shadow: 1px 1px 0px rgb(0 0 0 / 20%);
    }
}

@media screen and (min-width: 1200px) {
    .box-shadow-expierens {
        box-shadow: 1px 1px 0px rgb(0 0 0 / 20%);
    }
}

